<template>
  <div class="pos">
        <el-row>
            <el-col :span="7" class="pos-order"  id="order-list">
                <el-tabs  >
                    <el-tab-pane label="点餐">
                        <el-table  border  :data="tableData">
                            <el-table-column  prop="goodsName"  label="商品名称"></el-table-column>
                            <el-table-column  prop="count"  label="数量" width="50"></el-table-column>
                            <el-table-column  prop="price"  label="金额" width="70"></el-table-column>
                            <el-table-column    label="操作" width="100"  fixed="right">
                                <template scope="scope">
                                  <el-button type="text" size="small"  @click="delSingleGoods(scope.row)">删除</el-button>
                                  <el-button type="text" size="small"  @click="addOrderList(scope.row)" >增加</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="total">
                          <small>数量:</small>{{totalCount}}
                           <small>金额：</small>{{totalMoney}} 元
                        </div>
                       <div class="div-btn">
                         <el-button  type="warning" >挂单</el-button>
                         <el-button  type="danger"   @click="delAll()">删除</el-button>
                         <el-button  type="success"  @click="checkout()" >结账</el-button>

                       </div>

                    </el-tab-pane>

                    <el-tab-pane label="挂单">
                          挂单
                    </el-tab-pane>

                    <el-tab-pane label="外卖">
                      外卖
                  </el-tab-pane>

                </el-tabs>

            </el-col>
            <el-col :span="17">
              <div class="often-goods">
                <div class="title">常用商品</div>
                <div class="often-goods-list">
                  <ul>
                    <li  v-for="goods  in oftenGoods"  @click="addOrderList(goods)">
                      <span>{{goods.goodsName}}</span>
                      <span class="o-price">￥{{goods.price}}元</span>
                    </li>
                  </ul>
                </div>
              </div>

                <div class="goods-type">
                  <el-tabs>
                      <el-tab-pane label="汉堡">
                          <div>
                            <ul class="cookList">
                              <li v-for="goods in type0Goods" @click="addOrderList(goods)">
                                <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                <span class="foodName">{{goods.goodsName}}</span>
                                <span class="foodPrice">￥{{goods.price}}元</span>
                              </li>

                            </ul>
                          </div>
                      </el-tab-pane>
                    <el-tab-pane label="小食 ">小食</el-tab-pane>
                    <el-tab-pane label="饮料">饮料</el-tab-pane>
                    <el-tab-pane label="套餐">套餐</el-tab-pane>
                  </el-tabs>
                </div>
            </el-col>

        </el-row>
   </div>
</template>
<script>
  import axios  from 'axios';
  export default {
    name: 'Pos',
    data(){
        return {
          tableData:[],
          oftenGoods:[],
          type0Goods:[],
          type1Goods:[],
          type2Goods:[],
          type3Goods:[],
          totalMoney:0,
          totalCount:0
        }
    },
    created:function () {
      axios.get('https://easy-mock.com/mock/5aa74591e15815629b72afe1/example/goods')
        .then(data=>{
          //成功
          this.oftenGoods=data.data.data.oftenGoods;
          this.type0Goods=data.data.data.type0Goods;
        })
        .catch(error=>{
        //失败
        })
    },
    mounted:function () {
      //所有dom加载完成之后执行
      var orderHeight=document.body.clientHeight;
      document.querySelector("#order-list").style.height=orderHeight+'px';
    },
    methods: {
        addOrderList(goods){
              //商品是否已经存在于列表中
            this.totalMoney=0;
            this.totalCount=0;


          let isHave=false;
          for (let i=0;i<this.tableData.length;i++){
                if(this.tableData[i].goodsId==goods.goodsId){
                  isHave=true
                }
          }
          //根据判断的值编写业务逻辑
          if (isHave){
                //改变列表中商品的数量
              let arr=this.tableData.filter(o=>o.goodsId==goods.goodsId)
              arr[0].count++;

          }else{
            //debugger
              let newGoods={
                  goodsId:goods.goodsId,
                  goodsName:goods.goodsName,
                  price:goods.price,
                  count:1
              }
              this.tableData.push(newGoods);
          }
          this.getAllmoney();

        },
        //删除单个商品
        delSingleGoods(goods){
              this.tableData=this.tableData.filter(o=>o.goodsId!=goods.goodsId);
              this.getAllmoney();
        },
        delAll(){
          this.tableData=[];
          this.totalCount=0;
          this.totalMoney=0;
        },
        getAllmoney(){
          //对价格进行计算
          this.totalCount=0;
          this.totalMoney=0;
          if(this.tableData){
            this.tableData.forEach((ele)=>{
              this.totalCount+=ele.count;
              this.totalMoney=this.totalMoney+ (ele.price * ele.count);
            })
          }
        },
        checkout(){
          //结账
          if(this.totalCount!=0){
            this.tableData=[];
            this.totalCount=0;
            this.totalMoney=0;
            this.$message({
              message: '结账成功，感谢你又为店里出了一份力!',
              type: 'success'
            });

          }else{
            this.$message({
                  message:"不能空结",
                  type:"error"
            })
          }
        }
    }

  }
</script>
<style scoped>

  .pos-order{
      background: #f9fafc;
      border-right: 1px solid #c0ccda;
  }
  .div-btn{
      margin-top: 10px;
  }
  .often-goods{

  }
  .title{
      height: 20px;
    border-bottom: 1px solid #d3dce6;
    background: #f9fafc;
    padding:10px ;
    text-align: left;
  }
  .often-goods-list ul li{
      list-style: none;
      float: left;
      border: 1px solid #e5e9f2;
      padding: 10px;
      margin: 10px;
    background: #fff;
  }
  .o-price{
    color: #58b7ff;
  }
  .goods-type{
      clear: both;

  }
  .cookList li{
    list-style: none;
    width:23%;
    border:1px solid #E5E9F2;
    height: auto;
    overflow: hidden;
    background-color:#fff;
    padding: 2px;
    float:left;
    margin: 2px;
  }
  .cookList li span{
    display: block;
    float:left;
  }
  .foodImg{
    width: 40%;
  }
  .foodName{
    font-size: 18px;
    padding-left: 10px;
    color:brown;
  }
  .foodPrice{
    font-size: 16px;
    padding-left: 10px;
    padding-top:10px;
  }
  .total{
    background: #fff;
    padding:10px ;
    border-bottom: 1px solid #E5E9F2;
  }
</style>



